@import url(https://fonts.googleapis.com/css?family=Barlow:400,500,600,700|Barlow+Semi+Condensed:400,600,700&display=swap);
.App {
  font-size: 14px;
  font-family: 'Barlow', sans-serif;
  padding-bottom: 2em;
  color: #111;
}

.App header h1 {
  /* padding: 0.8em 1.2em; */
  margin: 0.8em 0 1.2em 0;
}

.App header h2 {
  margin: 1em 0 0.5em 0;
}

.App a {
  color: #076ad4;
  font-weight: 600;
}

.App hr {
  margin: 2rem 1rem 2rem 1rem;
}

.App .alert-info {
  color: #16333e;
}

.App .alert-light {
  color: #2d2d2e;
}

.App .alert strong {
  font-weight: 600;
}

.Header {

}

.Header .navbar .navbar-nav a.nav-link,
.Header .navbar .navbar-pills a.nav-link {
  padding-left: 1.2em;
  padding-right: 1.2em;
  margin: 2px 8px;
  color: #076ad4;
  display: block;
}

.Header .navbar .navbar-nav a.nav-link:hover {
  /* background-color: #e0e7ed; */
  text-decoration: underline;
  /* border-bottom: 2px solid #d0d7df; */
}

.Header .navbar .nav-pills a.nav-link.active {
  /* border-bottom: 3px solid #DFD7CA; */
  color: #fff;
  background-color: #045bb9;
}

.Stage {
  width: 100%;
  min-height: 480px;
  margin-bottom: 1.5rem;
}

.Stage .card-header {
  font-size: 1.2rem;
  font-weight: 500;
}

.Stage .axis-title {
  color: #444;
  padding-right: 1rem;
  font-size: 0.9em;
  font-family: 'Barlow', 'Barlow Semi Condensed', 'Roboto';
}

.Legend {
  position: absolute;
  right: 0;
  bottom: 0;
  margin-right: 0.5em;
  margin-bottom: 1.7em;
  border: 1px solid #ddd;
  z-index: 1000;
  padding: 0.5em;
  font-size: 1.0rem;
  font-family: 'Barlow Semi Condensed';
  background-color: #ffffffee;
}

.Legend div.vx-legend-shape > div {
  border: 1px solid #888;
}

.Tooltip {
  background-color: #333;
  color: white;
  line-height: 0.8em;
  font-size: 0.9em;
  opacity: 0.7;
  padding: 4px;
  border-radius: 4px;
  z-index: 1000;
  transform: translateX(-30%) translateY(1.6rem);
  position: relative;
  min-width: 100px;
}

.Tooltip h5 {
  margin-bottom: 0.2rem;
  font-size: 0.9rem;
  font-weight: 600;
}

.Tooltip p {
  margin-bottom: 0.1rem;
}

.Chart {
  font-family: 'Barlow Semi Condensed', 'Roboto', sans-serif;
}

.Chart .axis {

}

.Chart line.axis {
  /* stroke: #bbb; */
  /* stroke-width: 2px; */
}

.Chart .axis text {
  fill: #444;
}

.Chart .axis path {
  stroke: lightgrey;
}

.Chart .axis line.axis-baseline {
  stroke: #bbb;
  stroke-width: 2px;
}

.Chart g.ordinal-labels {
  vertical-align: middle;
}

.Chart .axis-label text, .Chart .ordinal-labels text {
  font-size: 1.1rem;
}

.Chart .axis-title text {
  font-family: 'Barlow', 'Barlow Semi Condensed', 'Roboto';
}

.Chart g.direct-label text {
  font-size: 1rem;
  font-weight: 700;
  text-anchor: end;
  /* fill: white; */
}

.Chart g.direct-label.light text {
  fill: white;
}

.Chart g.direct-label.dark text {
  fill: #303030;
}

.Chart.Choropleth {
  width: 100%;
  position: relative;
}

.Chart.Scatterplot {
  position: relative;
}

.Chart .leaflet-container {
  width: 100%;
  min-height: 500px;
  margin: 0 auto;
}

.Controls {
  margin-top: 0.5rem;
}

.Controls select.custom-select {
  color: #343a3f;
}

.SparkControls {
  display: inline;
  color: #333;
  margin-top: 0;
}

.Intro {

}

.DataTable {

}

.DataTable table {
  margin: 0;

}

.DataTable table .table-bordered td,
.DataTable table .table-bordered th {
  border-color: #c3cce2;
}

.DataTable .spark {
  display: inline-block;
  background-color: #A3DBFF;
}

/* #E58606,#5D69B1,#52BCA3,#99C945,#CC61B0,#24796C,#DAA51B,#2F8AC4,#764E9F,#ED645A,#CC3A8E,#A5AA99 */

.DataTable .spark .sparktext {
  margin-left: 0.3em;
  font-size: 0.85em;
  color: #111;
  white-space: nowrap;
}

.DataTable table th.position-sticky,
.DataTable table td.position-sticky {
  left: 0;
  z-index: 1;
  /* border-width: 1px 2px 1px 1px; */
  background-clip: padding-box;
  border-collapse: collapse;
}
.DataTable table th.position-sticky {
  border-width: 1px 2px 2px 1px;
}
.DataTable table td.position-sticky {
  background-color: white;
  border-width: 1px 2px 1px 1px;
}

.Dash {

}

.Dash .second {
  background-color: #e5e9f2;
  border: 1px solid #cbcadf;
  border-radius: 3px;
  padding-top: 1em;
}

.Dash .second + div.Controls {
  padding-top: 0;
}

.Profile {
  
}

.Profile table {
  font-size: 1.1rem;
}

.Profile table th,
.Profile table td {
  padding: 0.5em;
}

.Footer {

}

.Footer a {
  /* color: #0791e9 !important; */
}

body {
  margin: 0;
